import { FC } from 'react';
import ReactApexChart from 'react-apexcharts';

export const ControversialLevelsChart: FC = () => {


     const chartData =  {
          series: [{
            name: 'Net Profit',
            data: [16, 40, 32, 12, 0]
          }],
          options: {
            chart: {
              type: 'bar',
              height: 350
            },
            legend: {
              show: true
            },
            plotOptions: {
              bar: {
                horizontal: false,
                columnWidth: '10%',
                endingShape: 'rounded'
              },
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              show: true,
              width: 0,
              colors: ['transparent']
            },
            xaxis: {
              categories: ['1','2','3','4','5'],
            },
            yaxis: {
              title: {
                text: '$ (thousands)'
              }
            },
            fill: {
              opacity: 1
            },
          },
          colors: ['#003F7A'],
        };
   return(
    <>
        <ReactApexChart type="bar" height={350} options={chartData} series={chartData.series} />
    </>
   )
}
