const data4 = ['Morningstar Sustainbility Rating'];

// Export esg panel
export const ESGPanel: FC = () => {
  // States
  const [collapseStatus, setCollapseStatus] = useState([false, false, false, false]);

  // Toggle collapse handler
  const handleToggleCollapse = (index: number) => {
    collapseStatus[index] = !collapseStatus[index];
    setCollapseStatus([...collapseStatus]);
  };

  // Return esg panel
  return (
    <>
      {menuList.map((menu, index) => (
        <React.Fragment key={index}>
          <MenuItem className='_MenuItem' onClick={() => handleToggleCollapse(index)}>{menu}</MenuItem>
          <Collapse in={collapseStatus[index]}>
            {index === 0 && (
              <>
                <List className='scroll-mobile controversial_business_areas'>
                  <ListItem className='list_heading_style' key={index}>
                    <ListItemAvatar>
                      <span></span>
                    </ListItemAvatar>
                    <ListItemText className='title'>Ausschlusskriterien</ListItemText>
                    <ListItemText sx={{ textAlign: 'right' }}>
                      <div className='Tooltip_wrapper'>
                        Kein Umsatz im Geschäftsfeld
                        <Tooltip title="Die Prozentzahlen in dieser Spalte geben den Anteil der Unternehmen im Portfolio an, die gar keinen Umsatz in dem jeweiligen Geschäftsfeld erzielen." placement="top">
                          <InfoSharpIcon/>
                        </Tooltip>
                      </div>
                    </ListItemText>
                    <ListItemText sx={{ textAlign: 'right' }}>
                      <div className='Tooltip_wrapper'>
                        Umsatz &lt;5%
                        <Tooltip title="Die Prozentzahlen in dieser Spalte geben den Anteil der Unternehmen an, die zwischen 0% und 4,9% ihrer Umsätze in dem jeweiligen Geschäftsfeld erzielen. 4,9% des Umsatzes in einem Geschäftsfeld, ist der maximal zulässige Anteil, der unter dem Gesichtspunkt der Verhältnismäßigkeit toleriert wird. Das gilt nicht für geächtete Waffen, Ölsand und Fracking - in diesen Bereichen greift ein absolutes Investmentverbot." placement="top">
                          <InfoSharpIcon/>
                        </Tooltip>
                      </div>
                    </ListItemText>
                    <ListItemText sx={{ textAlign: 'right' }}>
                      <div className='Tooltip_wrapper'>
                        Umsatz &gt;5%
                        <Tooltip title="Die Prozentzahlen in dieser Spalte geben den Anteil der Unternehmen an, die mehr als 5% ihrer Umsätze in dem jeweiligen Geschäftsfeld erzielen. Mehr als 5% ist nach unseren Kriterien nicht zulässig." placement="top">
                          <InfoSharpIcon/>
                        </Tooltip>
                      </div>
                    </ListItemText>
                  </ListItem>
                  {data1.map(({ icon, title, business_segment, conversion_lt_5, conversion_gt_5 }, index) => (
                    <ListItem key={index}>
                      <ListItemAvatar>
                        <span className={`${icon}`}></span>
                      </ListItemAvatar>
                      <ListItemText className='title'>{title}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{business_segment}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{conversion_lt_5}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{conversion_gt_5}</ListItemText>
                    </ListItem>
                  ))}
                </List>
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: 30.06.2022</Typography>
                </Stack>
              </>
            )}
            {index === 1 && (
              <Stack mt={5} spacing={5} divider={<Divider />}>
                <ControversialLevelsChart/>
                <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: 30.06.2022</Typography>
              </Stack>
            )}
            {index === 2 && (
              <>
                <List>
                  {data2.map(({ a, b }, index) => (
                    <ListItem key={index}>
                      <ListItemText>{a}</ListItemText>
                      <ListItemText sx={{ textAlign: 'right' }}>{b}</ListItemText>
                    </ListItem>
                  ))}
                </List>
                <Stack mt={5} spacing={5} divider={<Divider />}>
                  <Typography>Diese Werte können sich im Laufe der Zeit ändern.</Typography>
                  <Typography>Quelle: Eigene Berechnungen Shareholder Value Management AG. Stand der Daten: 30.06.2022</Typography>
                </Stack>
              </>
            )}
            {index === 3 && (
              <Table sx={{ marginY: 10 }}>
                <TableBody>
                  {data3.map((d, index) => (
                    <TableRow key={index}>
                      <TableCell>{d}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}
            {index === 4 && (
              <>
                <Table>
                  <TableBody>
                    {data4.map((d, index) => (
                      <TableRow key={index}>
                        <TableCell>{d}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
                <Typography>
                  © 2022 Morningstar. Alle Rechte vorbehalten. Weitere detailliertere Informationen über das Morningstar
                  Sustainability Rating*, auch über seine Methodik, finden Sie auf: https://bit.ly/3078bQt. Aus 3038 EUR
                  Flexible Allocation - Global Fonds, Stand: 31.03.22. Basierend auf 93,73% der AUM von „Unternehmen“
                  und 100,00% der AUM von „Staaten“. Die Daten beruhen nur auf Long-Positionen.
                </Typography>
              </>
            )}
          </Collapse>
        </React.Fragment>
      ))}
    </>
  );
};
